<template >
    <v-card class="px-4" outlined >
                <v-card-text v-if="previewForm && !showStats">
            <v-form ref="loginForm"  lazy-validation >
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    id="title"
                    v-model="title"
                    label="Product Name"
                    :rules="rules.name"
                    required
                    counter="12"
                    @keyup="checkButton"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                  id="description"
                    v-model="description"
                    label="Description"
                     @keyup="checkButton"
                      :rules="rules.name"
                      counter="200"
                    required
                  ></v-textarea>
                </v-col>
                <v-col cols="12">
                  <v-select
                      id="category"
                     :items="items"
                     label="Category"
                     v-model="category"
                      @change="checkButton"
                    ></v-select>
                </v-col>
                <v-col cols="12">
                  <v-file-input
                    id="image"
                    accept="image/*"
                    label="File input"
                    @change="onFileChange"
                    ></v-file-input>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    id="profit"
                    v-model="desiredSum"
                    label="Desired Minimum Profit"
                    prefix="£"
                    @keyup="checkButton"
                     :rules="rules.name"
                     number
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                    <v-subheader class="pl-0">
                     Price per ticket
                    </v-subheader>
                  <v-slider
                  @change="checkButton"
                    v-model="ticketPrice"
                    step="1"
                    thumb-label
                    :max="5"
                    :min="1"
                    ticks
                ></v-slider>
                </v-col>
                <v-col cols="12">
                <v-text-field
                    id="ticketNumber"
                    @keypress="checkReccomended"
                    @keyup="checkButton"
                    :color="color"
                    :hint="reccomendMessage"
                    v-model="ticketNumber"
                    label="Number of Tickets put up for sale"
                    required
                    :rules="rules.name"
                  ></v-text-field>
                  </v-col>
                  <!-- <v-col cols="12">
                      <v-img
                     :src="image"
                     aspect-ratio="1.7"
                     ></v-img>
                      </v-col> -->
                <v-col class="d-flex" cols="12" sm="6" xsm="12"> </v-col>
                <v-spacer></v-spacer>
                <v-col cols="12">
                  <v-btn v-if="!disable"
                    id="stats"
                    block  large text
                    color="success"
                    @click="viewStats"
                    
                    ><v-icon left>mdi-calculator</v-icon>View Statistics</v-btn
                  >
                  <v-btn v-if="!disable"
                    id="preview"
                    block  large text
                    color="success"
                    @click="checkProduct"
                    
                    ><v-icon left>mdi-file-find</v-icon>Preview Item</v-btn
                  >
                  <v-btn v-if="!disable"
                    id="savebtn"
                    block  large outlined
                    color="success"
                    @click="uploadImage"
                    
                    ><v-icon left>save</v-icon>Save</v-btn
                  >
                  <v-btn v-if="disable"
                    block  large outlined
                    disabled
                    color="success"
                    
                    
                    
                    ><v-icon left>save</v-icon>Save</v-btn
                  >
                </v-col>
              </v-row>
              <v-row>
                <v-col class="d-flex" cols="12">
                  <p class="success" id="successmessage">{{message }}</p>
                </v-col>
                </v-row>
                </v-form>
                </v-card-text>
                <!-- ******************************************************************************************************************* -->
                <v-card-text class="px-4" outlined v-if="!previewForm && showStats">
                  <v-card>
                    <v-card-title>
                      <v-col cols="12">
                  <v-text-field
                    v-model="desiredSum"
                    label="Desired Minimum Profit"
                    prefix="£"
                    @keyup="checkButton"
                     :rules="rules.name"
                     number
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                    <v-subheader class="pl-0">
                     Price per ticket
                    </v-subheader>
                  <v-slider
                  @change="checkButton"
                    v-model="ticketPrice"
                    step="1"
                    thumb-label
                    :max="5"
                    :min="1"
                    ticks
                ></v-slider>
                </v-col>
                <v-col cols="12">
                <v-text-field
                    @keydown="checkReccomended"
                    @keyup="checkButton"
                    :color="color"
                    :hint="reccomendMessage"
                    v-model="ticketNumber"
                    label="Number of Tickets put up for sale"
                    required
                    :rules="rules.name"
                  ></v-text-field>
                  <!-- stats******************************************************************************************************** -->
                  <v-card v-if="showPredicted" outlined>
                  <!-- <v-chip-group> -->
                    <v-card-title>Data:</v-card-title>
                    <v-card-text>Category chosen: {{this.category}}</v-card-text>
                    <v-card-text>Profit Desired: £{{this.desiredSum}}</v-card-text>
                    <v-card-text>The price of a ticket: £{{this.ticketPrice}}/ticket</v-card-text>
                    <v-card-text>Your Number of tickets: {{this.ticketNumber}}</v-card-text>
                    <v-card-text>Minimum tickets sold required to have £{{this.desiredSum}} profit: {{this.ticketsRequired}}</v-card-text>
                    <v-card-text>Chance of selling the Item on the first {{this.ticketsRequired}} ticket(s): <{{this.chance}}%</v-card-text>
                    <v-card-text>Predicted Number of tickets sold: {{this.predictedTicketsSold}} ticket(s)</v-card-text>
                    <v-card-text >Chance of selling the item on the first {{this.predictedTicketsSold}} ticket(s): <{{this.chancePredicted}}%</v-card-text>
                    <!-- </v-chip-group> -->
                 </v-card>
                 <v-card v-if="!showPredicted" outlined>
                  <!-- <v-chip-group> -->
                    <v-card-title>Data:</v-card-title>
                    <v-card-text>Category chosen: {{this.category}}</v-card-text>
                    <v-card-text>Profit Desired: £{{this.desiredSum}}</v-card-text>
                    <v-card-text>The price of a ticket: £{{this.ticketPrice}}/ticket</v-card-text>
                    <v-card-text>Your Number of tickets: {{this.ticketNumber}}</v-card-text>
                    <v-card-text>Minimum tickets sold required to have £{{this.desiredSum}} profit: {{this.ticketsRequired}}</v-card-text>
                    <v-card-text>Chance of selling the Item on the first {{this.ticketsRequired}} ticket(s): <{{this.chance}}%</v-card-text>
                    <v-card-text>Predicted Number of tickets sold: {{this.predictedTicketsSold}} ticket(s)</v-card-text>
                    <!-- </v-chip-group> -->
                 </v-card>
                  </v-col>
                    </v-card-title>
                  </v-card>
                  <v-btn 
                    block  large text
                    color="success"
                    @click="backToForm"
                    id="backtoform"
                    
                    ><v-icon left>mdi-arrow-left</v-icon>Go Back</v-btn
                  >
                </v-card-text>
        <v-card-text class="px-4" outlined v-if="!previewForm && !showStats">
            <!-- ******************************************************************************************************* -->
            <v-row justify="center">
                    <v-col
                    sm="6" 
                    md="4"
                    
                    >
                    <!-- {{product}} -->
                        <v-card outlined class="product" @click.stop="expand = !expand">
                            <v-card-text >7 days left of the competition!</v-card-text>
                            <v-img :src="image" height="200px"></v-img>
                        <v-card-title>{{title}}</v-card-title>
                        <v-card-subtitle>£{{ticketPrice}}</v-card-subtitle>
                       
                        <v-card-actions>
                            <v-btn color="success" outlined>
                                <v-icon small left>mdi-arrow-expand-all</v-icon>
                                Details
                            </v-btn>
                            
                        </v-card-actions>
                        </v-card>
                        
                    </v-col>
                </v-row>
            <!-- ******************************************************************************************************** -->
            <v-row justify="center">
                <v-dialog v-model="expand" width="auto " :fullscreen="$vuetify.breakpoint.xsOnly">
                <!-- <v-col sm="10" offset-sm="1"  md="8" offset-md="2" > -->
                    
                        <v-card >
                            
                                   <v-card-title>7 days left of the competition!</v-card-title>
                            <v-img :src="image" min-height="100px" max-height="400px">
                                <v-btn absolute right @click.stop="expand = !expand"><v-icon >mdi-close-circle</v-icon></v-btn>
                            </v-img>
                                <v-tabs
                             v-model="tab"
                             show-arrows
                             background-color="deep-purple accent-4"
                             icons-and-text
                             dark
                             grow
                                >
                            <v-tabs-slider color="purple darken-4"></v-tabs-slider>
                            <v-tab v-for="i in tabs" :key="i.name">
                            <v-icon large>{{ i.icon }}</v-icon>
                               <div class="caption py-1">{{ i.name }}</div>
                              </v-tab>
                               <v-tab-item>
                                   <v-card>

                            <v-card-title>{{title}}</v-card-title>
                            <!-- <v-row> -->
                            <!-- </v-row> -->
                            <v-card-subtitle>{{category}}</v-card-subtitle>
                            <v-spacer></v-spacer>
                        <v-card-subtitle>£{{ticketPrice}} to play.</v-card-subtitle>
                        <v-card-text>{{ticketNumber}} tickets available!</v-card-text>
                        <v-card-actions>
                            <v-btn  block color="error"  large outlined>
                                <v-icon small left>mdi-play</v-icon>
                                Play Now!
                            </v-btn>
                            <v-btn  block color="error" large outlined>
                                <v-icon small left>mdi-play</v-icon>
                                Log In to Play!
                            </v-btn>
                            
                        </v-card-actions>
                                   </v-card>
                         </v-tab-item>
                          <v-tab-item>
                            <v-card-title>{{title}}</v-card-title>
                            <!-- <v-row> -->
                            <!-- </v-row> -->
                            <v-spacer></v-spacer>
                        <v-card-text>{{description}}</v-card-text>
                        <v-card-actions>
                            
                        </v-card-actions>
                         </v-tab-item>
                            </v-tabs>
                        </v-card>
                    
                <!-- </v-col> -->
                </v-dialog>
            </v-row>
            <!-- ******************************************************************************************************** -->
            <v-btn 
                    block  large text
                    color="success"
                    @click="backToForm"
                    id="backtoform"
                    ><v-icon left>mdi-arrow-left</v-icon>Go Back</v-btn
                  >
        </v-card-text>
        
        </v-card>
</template>
<script>
import axios from 'axios';
import {v4 as uuidv4} from 'uuid'
import { mapActions, mapGetters } from "vuex";
const MAX_IMAGE_SIZE=1000000;
const API_ENDPOINT = 'https://5nvt9nu86e.execute-api.us-east-1.amazonaws.com/default/getPresignedURL';
export default {
        data(){
            return{
              showStats:false,
              expand:false,
               tab: 0,
            date:"",
    tabs: [
        {name:"Play", icon:"mdi-play"},
        {name:"Details", icon:"mdi-text"},
       
    ],
                rules:{
                  name:[val=>(val||"").length>0||"This field is required"],
                  // counterTitle:[v => v.length <= 12 || 'Max 12 characters'],
                  // counterDescription:[v => v.length <= 200 || 'Max 200 characters'],
                },
                showPredicted:false,
                reccomendMessage:"",
                color:"",
                uploadURL:"",
                message:"",
                error:"",
                productPrice: "",
                short: "",
                description: "",
                title: "",
                ticketPrice: 1,
                ticketNumber: 0,
                predictedTicketsSold: 0,
                category: "automobiles",
                username: this.$store.getters.user.username,
                image: "",
                chance: 0,
                chancePredicted:0,
                disable:true,
                average: 0,
                desiredSum: 0,
                previewForm:true,
                ticketsRequired:0,
                items:["automobiles","charity","clothing","holidays","jewelry","housing","sports","technology"],
                dummyData: {
                category: {
                automobiles: {
                    averageTicketSalePerItem: 340,
                },
                charity: {
                    averageTicketSalePerItem: 120,
                },
                clothing: {
                    averageTicketSalePerItem: 252,
                },
                holidays: {
                    averageTicketSalePerItem: 56,
                },
                jewelry: {
                    averageTicketSalePerItem: 12,
                },
                housing: {
                    averageTicketSalePerItem: 542,
                },
                sports: {
                    averageTicketSalePerItem: 89,
                },
                technology: {
                    averageTicketSalePerItem: 752,
                },
                },
                },

            }
        },
        methods:{
            ...mapActions(["addProduct","fetchProducts"]),
              expandedProduct(){
                this.expand=true;
              },
              calculateChanceReccomendation(desired,price){
                  this.ticketNumberRecommendation=parseInt(desired/price)*100;
                 
                 
                 if (this.category == "automobiles") {
                this.average = this.dummyData.category.automobiles.averageTicketSalePerItem;
               } else if (this.category == "charity") {
                this.average = this.dummyData.category.charity.averageTicketSalePerItem;
                  } else if (this.category == "clothing") {
                this.average = this.dummyData.category.clothing.averageTicketSalePerItem;
                 } else if (this.category == "holidays") {
                 this.average = this.dummyData.category.holidays.averageTicketSalePerItem;
                } else if (this.category == "jewelry") {
                  this.average = this.dummyData.category.jewelry.averageTicketSalePerItem;
                } else if (this.category == "housing") {
                  this.average = this.dummyData.category.housing.averageTicketSalePerItem;
                } else if (this.category == "sports") {
                  this.average = this.dummyData.category.sports.averageTicketSalePerItem;
                } else if (this.category == "technology") {
                  this.average = this.dummyData.category.technology.averageTicketSalePerItem;
                }
                  return this.ticketNumberRecommendation;
              },
              calculateChanceInput(desired,tickets,price,average){
                  this.chance=100-Math.floor((parseInt(tickets-(desired/price))/tickets)*100);
                  if(this.chance>100){
                    this.chance=100;
                  }else if(this.chance<0){
                    this.chance=0;
                  }
                  this.ticketsRequired=Math.round(desired/price);

                  if(tickets<average){
                    console.log("smaller")
                    this.predictedTicketsSold=tickets;
                    this.showPredisted=false;
                  }else if(tickets>average){
                    console.log("bigger")
                    this.predictedTicketsSold=average;
                    this.showPredicted=true;
                  }
                  this.chancePredicted=100-Math.floor((parseInt(tickets-(this.predictedTicketsSold/price))/tickets*100))
                  if(this.chancePredicted>100){
                    this.chancePredicted=100;
                  }else if(this.chancePredicted<0){
                    this.chancePredicted=0;
                  }
                  return this.chance;
              },
              
              viewStats(){
                  this.calculateChanceReccomendation(this.desiredSum,this.ticketPrice);
                  this.calculateChanceInput(this.desiredSum,this.ticketNumber,this.ticketPrice);

                  this.showStats=true;
                  this.previewForm=false;
              },
            checkProduct(){
              this.previewForm=false;
            },
            backToForm(){
              this.previewForm=true;
              this.showStats=false;
            },
            checkReccomended(){
              // if(this.ticketNumberRecommendation!=""||this.ticketNumberRecommendation!=null){

                if(this.ticketNumber<(parseInt(this.desiredSum/this.ticketPrice)*100)){
                    this.color="orange"
                    this.checkButton();
                    this.reccomendMessage=`WARNING! The system reccomends ${parseInt(this.desiredSum/this.ticketPrice)*100} tickets.`
                }else if(this.ticketNumber>=(parseInt(this.desiredSum/this.ticketPrice)*100)){
                    this.color="success"
                    this.reccomendMessage=""
                    this.checkButton();
                }
              // }
            },
             onFileChange(file) {
        //   e.preventDefault();
        console.log(file)
    //   let files = e.target.files || e.dataTransfer.files
    //   if (!files.length) return
    //   this.createImage(files[0])
          this.createImage(file)
    },
    createImage (file) {
      // var image = new Image()
      let reader = new FileReader()
      reader.onload = (e) => {
        console.log('length: ', e.target.result.includes('data:image/jpeg'))
        if (!e.target.result.includes('data:image/jpeg')) {
          return alert('Wrong file type - JPG only.')
        }
        if (e.target.result.length > MAX_IMAGE_SIZE) {
          return alert('Image is loo large - 1Mb maximum')
        }
        this.image = e.target.result
      this.checkButton();
      }
      reader.readAsDataURL(file)
    },
    checkButton(){
            if(this.image=="" ||this.title=="" || this.description=="" || this.desiredSum<=0 || this.ticketNumber<=0||this.title.length>12||this.description.length>200){
            this.disable=true;
            this.calculateChanceReccomendation(this.desiredSum,this.ticketPrice);
            this.calculateChanceInput(this.desiredSum,this.ticketNumber,this.ticketPrice,this.average);
          }else{
            this.disable=false;
            this.calculateChanceReccomendation(this.desiredSum,this.ticketPrice);
            this.calculateChanceInput(this.desiredSum,this.ticketNumber,this.ticketPrice,this.average);
         }
         
          },
     onSubmitProduct() {
      // e.preventDefault();
      // console.log(e)
      let myuuid=uuidv4();
  console.log("submit pressed")
      let product = { //whenever you have a v-model with a property, it needs added to data
        id:myuuid,
        description: this.description,
        title: this.title,
        ticketPrice: this.ticketPrice,
        ticketsSold: 0,
        ticketNumber: this.ticketNumber,
        category: this.category,
        cognitoUsername: this.$store.getters.user.username,
        img_url: this.image,
        productPrice: this.desiredSum,
        date:new Date()
      };
      // console.log(product)
      this.addProduct(product);
      
    },
          uploadImage: async function (e) {
      e.preventDefault();
      console.log('Upload clicked')
      console.log(e)
      // Get the presigned URL
      const response = await axios({
        method: 'GET',
        url: API_ENDPOINT
      })
      console.log('Response: ', response.data)
      console.log('Uploading: ', this.image)
      let binary = atob(this.image.split(',')[1])
      let array = []
      for (var i = 0; i < binary.length; i++) {
        array.push(binary.charCodeAt(i))
      }
      let blobData = new Blob([new Uint8Array(array)], {type: 'image/jpeg'})
      console.log('Uploading to: ', response.data.uploadURL)
      const result = await fetch(response.data.uploadURL, {
        method: 'PUT',
        body: blobData
      })
      console.log('Result: ', result)
      // Final URL for the user doesn't need the query string params
      this.uploadURL = response.data.uploadURL.split('?')[0]
      console.log("link: ",this.uploadURL) //link to image
      // save image link in variable
      this.image=this.uploadURL;
      // this.message="Image Uploaded Succesfully!"
      //       try {
      //   const user = await Auth.currentAuthenticatedUser();
      //   await Auth.updateUserAttributes(user, {
      //     "custom:profileImage": this.state.image,
      //   });
      //   this.state.message = "Profile Picture Uploaded Succesfully";
      // } catch (error) {
      //   this.state.error = error;
      // }
      try {
        this.onSubmitProduct();
        this.message="Product Added Succesfully!"
        this.reccomendMessage="";
                this.color="";
                this.uploadURL="";
                this.error="";
                this.productPrice= "";
                this.short= "";
                this.description= "";
                this.title= "";
                this.ticketPrice= 1;
                this.ticketNumber= "";
                this.ticketNumberRecommendation= "";
                this.category= "automobiles";
                this.username= this.$store.getters.user.username;
                this.image= "";
                this.chance= 0;
                this.disable=true;
                this.predictedTicketsSold= 0;
                this.desiredSum= 0;
                this.previewForm=true;
      } catch (error) {
        this.message=error
      }
        // this.$router.push({ name: 'Account' })
    },
        },
        computed: mapGetters(["allMessages"]),
        mounted() {
          
        },
        
}
</script>
