export const  mutations={
    setProducts:(state, products)=>(state.products=products),
    setAvailableProducts:(state,availableProducts)=>(state.availableProducts=availableProducts),
    setUserProducts:(state,userProducts)=>(state.userProducts=userProducts),
    newProduct:(state,product)=>state.products.unshift(product),
    newProductAvailable:(state,product)=>state.availableProducts.unshift(product),
    newProductUser:(state,product)=>state.userProducts.unshift(product),
    setRandoms:(state,randoms)=>(state.randoms=randoms),
    setMessage:(state,mess)=>(state.message=mess),
    setTicket:(state,tick)=>(state.ticket=tick),
    setAuthStatus:(state,status)=>(state.isAuthenticated=status),
    setUser:(state,username)=>(state.user=username),
    setIsAuthenticating:(state,value)=>(state.isAuthenticating=value),
    setAllUserWinningTickets:(state,value)=>(state.allUserWinningTickets=value),
    setAllUserLosingTickets:(state,value)=>(state.allUserLosingTickets=value)
};


export default{
    state,
    getters,
    actions,
    mutations
};