<template >
    <v-container mb-16>
        <v-row justify="center">
            <v-col md="10" >
                <v-card outlined>
                    <v-img src="https://www.brookes.ac.uk/assets/0/2147493288/2147493291/70860e98-194e-4aed-bafe-d57cfe3c45b5.jpg" height="200px">
                        
                    </v-img>
                </v-card>
            </v-col>
        </v-row>
        <v-row>
      
<v-row justify="center" @mousemove="checkPersonalData">

      <v-col  md="9" lg="7"  >
        <!-- <ProductDisplay v-bind:filter="this.filter"/> -->
        
          <v-row >
            <v-col  sm="6" md="4" >
              <div>
                <v-card outlined>
                 
                  <v-card-subtitle>Number of Products: {{this.totalProducts}}</v-card-subtitle>
                </v-card>
              </div>
              <!-- {{product}} -->
            </v-col>
            <v-col   sm="6" md="4"  >
              <div>
                <v-card outlined>
                 
                  <v-card-subtitle>Profit: £{{this.profit}}</v-card-subtitle>
                </v-card>
              </div>
              <!-- {{product}} -->
            </v-col>
            <v-col  sm="6" md="4"   >
              <div>
                <v-card outlined>
                 
                  <v-card-subtitle>Tickets Sold: {{this.ticketsSold}}</v-card-subtitle>
                </v-card>
              </div>
              <!-- {{product}} -->
            </v-col>
          </v-row>
        
      </v-col>
</v-row>
    </v-row>
    <v-row justify="center">
        <v-col md="10" lg="10"  >

        <div>
    <v-tabs
      v-model="tab"
      show-arrows
      background-color="deep-purple accent-4"
      icons-and-text
      dark
      grow
    >
      <v-tabs-slider color="purple darken-4"></v-tabs-slider>
      <v-tab v-for="i in tabs" :key="i.name" :id="i.id" >
        <v-icon large>{{ i.icon }}</v-icon>
        <div class="caption py-1">{{ i.name }}</div>
      </v-tab>
      <v-tab-item>
          <v-card class="px-4" outlined>
                <v-card-text>
            <v-form ref="loginForm"  lazy-validation>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.email"
                    label="E-mail/Username(not changeable)"
                    readonly
                    id="accountUsername"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.postCode"
                    label="Post Code"
                    
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.addressLine1"
                    label="Address Line 1"
                    
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.addressLine2"
                    label="E-mail/Username"
                    
                  ></v-text-field>
                </v-col>
                <v-col class="d-flex" cols="12" sm="6" xsm="12"> </v-col>
                <v-spacer></v-spacer>
                <v-col cols="12">
                  <v-btn
                    block  large outlined
                    color="success"
                    @click="onSubmitUpdate"
                    ><v-icon left>save</v-icon>Save</v-btn
                  >
                </v-col>
              </v-row>
              <v-row>
                <v-col class="d-flex" cols="12">
                  <p class="success">{{ state.message }}</p>
                </v-col>
                </v-row>
                </v-form>
                </v-card-text>
        </v-card>
      </v-tab-item>
      <v-tab-item>
        <v-row>
                    <v-col
                    sm="6" 
                    md="4"
                    v-for="product in allUserProducts"
                    :key="product.productId"
                    >
                    <!-- {{product}} -->
                        <v-card outlined class="product">
                            <v-card-text > {{7 - Math.floor((date - new Date(`${product.date}`)) / 86400000)}} days left of the competition!</v-card-text>
                            <v-img :src="product.img_url" height="200px"></v-img>
                        <v-card-title>{{product.Title}}</v-card-title>
                        <v-card-subtitle>£{{product.ticketPrice}}</v-card-subtitle>
                       
                        <v-card-actions>
                            <v-btn color="success" outlined :id="product.Title" @click="deleteProduct(product.productId,product.Category,product.Seller,product.ticketNumber)">
                                <v-icon small left>mdi-delete</v-icon>
                               Delete
                            </v-btn>
                            
                        </v-card-actions>
                        </v-card>
                        
                    </v-col>
                </v-row>
      </v-tab-item>
      <v-tab-item>
          <!-- <v-card><h3>your products</h3></v-card> -->
          <addProduct/>
      </v-tab-item>
      <v-tab-item>
        <ticket/>
      </v-tab-item>
    </v-tabs>
    </div>
    </v-col>
    </v-row>
    </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import { Auth } from "aws-amplify";
import axios from 'axios';
import addProduct from '../components/accountPage/addProductForm'
import ticket from '../components/accountPage/tickets'
export default {
    components:{
      addProduct,
      ticket
    },
    data(){
        return{
            tab: 0,
    tabs: [
        {name:"Account Details", icon:"mdi-account",id:"accDets"},
        {name:"Your Products", icon:"mdi-store",id:"yourProducts"},
        {name:"Add Product", icon:"mdi-store-plus", id:"addProduct"},
        {name:"Your Tickets", icon:"mdi-ticket",id:"yourTickets"}
    ],
    totalProducts:0,
    profit:0,
    ticketsSold:0,
    state: {
        username: this.$store.getters.user.username,
        email: this.$store.getters.user.attributes.email,
        message: "",
        error: "",
        // image:require("../assets/undraw_profile_pic_ic5t.png"),
        postCode: this.$store.getters.user.attributes["custom:postCode"],
        addressLine1: this.$store.getters.user.attributes[
          "custom:addressLine1"
        ],
        addressLine2: this.$store.getters.user.attributes[
          "custom:addressLine2"
        ],
      },
      date: "",
      products: this.$store.getters.allUserProducts,
      username:this.$store.getters.user.username,

     
        }
    },
    mounted() {
      this.fetchProducts()
    },
    methods:{
      
      // deleteProduct(id,category,seller,ticketNumber){
      //   let params={
      //     "id":id,
      //     "name":seller,
      //     "ticketNumber":ticketNumber,
      //     "category":category,
      //   }
        
      //   this.handleDeleteProduct(params)
      // },
        ...mapActions(["fetchProducts", "fetchRandom", "handleDeleteProduct"]),
        checkPersonalData(){
          this.profit=0;
          this.ticketsSold=0;
          let self=this;
          this.allUserProducts.forEach(element=>{
            self.profit=self.profit+(element.ticketPrice*element.ticketsSold);
            self.ticketsSold=self.ticketsSold+element.ticketsSold;
          })
        },
        async onSubmitUpdate() {
      try {
        const user = await Auth.currentAuthenticatedUser();
        await Auth.updateUserAttributes(user, {
          "custom:postCode": this.state.postCode,
          "custom:addressLine1": this.state.addressLine1,
          "custom:addressLine2": this.state.addressLine2,
        });
        this.state.message = "Details Updated Successfully";
      } catch (error) {
        this.state.error = error;
      }
    },
    deleteProduct(id,category,seller,ticketNumber) {
      console.log(id);
      let obj = {
        id: id,
        name: seller,
        category: category,
        ticketNumber:ticketNumber
      };
      try {
        this.handleDeleteProduct(obj);
      } catch (error) {
        console.log(error);
      }
    },
    },
     computed: mapGetters(["allUserProducts", "allRandom", "isAuthenticated", "user"]),
     created() {
    console.log(this.username)
    // this.products=this.fetchProducts();
    this.fetchProducts();
    // this.fetchRandom();
    this.date = new Date();
    console.log("dada")
    console.log(this.products)
    this.totalProducts=this.allUserProducts.length
    let self=this;
    this.allUserProducts.forEach(element=>{
      self.profit=self.profit+(element.ticketPrice*element.ticketsSold);
      self.ticketsSold=self.ticketsSold+element.ticketsSold;
    })
  },
}
</script>
<style scoped>
    /* .info{
        display:flex;
        flex-direction: row;
        justify-content: center;
        align-content: space-between;
    } */
</style>