<template>
  <v-container ref="containerV" >
    <v-row v-if="displayCheckout">
      <v-col sm="6" offset-sm="3">
        <v-stepper v-model="step">
          <v-stepper-header>
            <v-stepper-step step="1" :complete="step > 1"> </v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="2" :complete="step > 2"> </v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="3"> </v-stepper-step>
          </v-stepper-header>
          <v-stepper-items>
            <v-stepper-content step="1">
              <v-text-field
                v-model="payment.nameOnCard"
                label="Name On Card"
                readonly
              />
              <v-text-field
                v-model="payment.addressOnCard"
                label="Address that Card is registered to"
                readonly
              />
              <v-text-field
                v-model="payment.phoneNumber"
                label="Phone Number"
                readonly
              />
              <v-btn color="primary" @click="next">
                Continue
              </v-btn>
              <v-btn text @click="$router.replace({ name: 'Home' })">
                Cancel
              </v-btn>
            </v-stepper-content>
            <v-stepper-content step="2">
              <v-text-field
                v-model="payment.accountNo"
                label="Account Number"
                readonly
              />
              <v-text-field
                v-model="payment.security"
                label="Security Pin"
                readonly
              />
              <v-btn color="primary" @click="next">
                Continue
              </v-btn>
              <v-btn text @click="previous">
                Go Back
              </v-btn>
            </v-stepper-content>
            <v-stepper-content step="3">
              <v-list disabled dense>
                <v-subheader class="title">
                  Review Details
                </v-subheader>
                <v-list-item-group color="primary">
                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title>
                        Name on Card: {{ payment.nameOnCard }}
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title>
                        Address: {{ payment.addressOnCard }}
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title>
                        Phone Number: {{ payment.phoneNumber }}
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title>
                        Account Number: {{ payment.accountNo }}
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title>
                        Security Pin: {{ payment.security }}
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
              <v-btn color="primary" @click="submit">
                Submit
              </v-btn>
              <v-btn text @click="previous">
                Go Back
              </v-btn>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
      </v-col>
    </v-row>
    <v-row justify="center" v-if="!displayCheckout">
      <v-col cols="12" md="6">
          <v-sheet
            elevation="12"
            
            
          >
          <!-- <div class="test"> -->

        <canvas ref="game" id="canvas" ></canvas>
          <!-- </div> -->
          
          </v-sheet>
          <v-row justify="center">
           <v-dialog v-model="displayMessage" persistent width="auto ">
                <v-card v-if="youWon">
                   <v-icon left>mdi-emoticon-happy-outline 
                  </v-icon>
                  <v-card-title  primary-title class="justify-center">
                  {{message}}
                  </v-card-title>
                  
                  <v-btn color="primary" @click="$router.push({ name: 'Home' })">
                Continue
              </v-btn>
              
                </v-card>
                <v-card v-if="gameOver">
                   <v-icon left>mdi-emoticon-sad-outline 
                  </v-icon>
                  <v-card-title  primary-title class="justify-center">
                  {{message}}
                  </v-card-title>
                  
                  <v-btn color="primary" @click="$router.push({ name: 'Home' })">
                Continue
              </v-btn>
              
                </v-card>
             </v-dialog>

          </v-row>
          <!-- <v-card> -->
              <!-- <v-content> -->

              <!-- </v-content> -->

          <!-- </v-card> -->
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import Vue from 'vue'
  import VueConfetti from 'vue-confetti'

  Vue.use(VueConfetti)
// window.addEventListener("resize", sayHello);
// function sayHello(){
//         this.canvas=document.getElementById("canvas")
//       this.ctx=this.canvas.getContext("2d")
      
//       this.canvas.width=this.canvas.parentElement.clientWidth;
//       this.canvas.height=this.canvas.width/2;
//     };
export default {
  data() {
    return {
      step: 1,
      displayMessage:false,
      payment: {
        nameOnCard: "exampleName",
        addressOnCard: "exampleAddress",
        phoneNumber: "07444444444",
        accountNo: 1234567891234567,
        security: 111,
      },
      displayCheckout: true,
      painting: false,
      objects: [],
      numberParticles: 0,
      numberParticlesDeleted: 0,
      percent: 0,
      square: {},
      canvas: document.getElementById("canvas"),
      ctx: "",
      ticketTypeUrl: "url('../assets/loosingTicket1.jpg')",
      message: "Scratch the marked area to find out if you won :)!",
      username: this.$store.getters.user.username,
      generateTicket: true,
      gameOver:false,
      youWon:false
    };
  },
  methods: {
      // continueToHomepage(){

      // },
    ...mapActions([
      "fetchRandomTicket",
      "handleUpdateProduct",
      "updateWinningTicket",
    ]),
    startPosition(e) {
      // console.log(1)
      this.painting = true;
      this.draw(e);
    },
    endPosition() {
      // console.log(2)
      this.painting = false;
      this.ctx.beginPath();
    },
    draw(e) {
      // console.log(3)
      let rect=this.canvas.getBoundingClientRect()
      let username = this.username;
      if (!this.painting) return;
        // console.log(this.ctx.canvas.offsetTop)
        // console.log(e.clientX- this.ctx.canvas.offsetLeft)
        // console.log(this.ctx.canvas.getBoundingClientRect().left)
        
      this.objects.forEach((element) => {
        if (
          element.coordX >= e.clientX - this.ctx.canvas.offsetLeft &&
          element.coordX <= e.clientX - this.ctx.canvas.offsetLeft + 20 &&
          element.coordY >= e.clientY - 7*this.ctx.canvas.offsetTop &&
          element.coordY <= e.clientY - 7*this.ctx.canvas.offsetTop + 20 &&
          element.existent == true
        ) {
          element.existent = false;
          this.numberParticlesDeleted++;
          this.percent = parseFloat(
            ((this.numberParticles - this.numberParticlesDeleted) /
              this.numberParticles) *
              100
          ).toFixed(2);
        }
      });
      console.log(this.percent + " %left");
      if (this.generateTicket && this.percent < 50) {
        let objTick = {
          "productId": this.$route.query.id,
          "totalTickets": this.$route.query.number,
          "ticketNumber":this.$route.query.number,
            "username":this.$route.query.username,
            "category":this.$route.query.category,
            "ticketsSold":this.$route.query.ticketsSold
        };
        console.log("this is object tick")
        console.log(objTick);
        this.handleUpdateProduct(objTick);
        console.log("product Updated")
        let randomTicket = this.fetchRandomTicket(objTick);
        let self = this;
        randomTicket.then(function(result) {
          console.log("this is just result" + result);
          console.log(result);
        if(result.length==0){
            self.message="Sorry but you have lost"
        }
          result.forEach((element) => {
            console.log("this is " + element);
            console.log(element);

            if ((
              element.Prize == "£1" ||
              element.Prize == "£2" ||
              element.Prize == "£3" ||
              element.Prize == "£4" ||
              element.Prize == "£5"
            )&&element.Winner=="") {
              // this.ticketTypeUrl="url(`../assets/winningTicket1.jpg`)"
              self.message = "Congratiulations! You have won " + element.Prize;
              document.getElementById("canvas").style.backgroundImage =
                "url(" +
                require("../assets/winningTicket" + element.Prize + ".jpg") +
                ")";
              // alert("You have won "+element.Prize)
              self.$confetti.start();
              self.displayMessage=true;
              self.youWon=true;
              setTimeout(function(){
                   self.$confetti.stop();
              },5000)
              let object = {
                ticketCode: element.Ticket_Code,
                winner: username,
                prodId: element.productId,
              };
              console.log(object);
              self.updateWinningTicket(object);
            } else if (element.Prize == "jackpot" && element.Winner == "") {
              // this.changeLink("url(`../assets/winningTicketBonus.jpg`)")
              // alert("You have won the jackpot")
              self.message =
                self.username + ", you have won the Jackpot! Congratiulations!";
              document.getElementById("canvas").style.backgroundImage =
                "url(" + require("../assets/winningTicketBonus.jpg") + ")";
              // alert("You have won "+element.Prize)
              self.$confetti.start();
              self.displayMessage=true;
              self.youWon=true;
              setTimeout(function(){
                   self.$confetti.stop();
              },5000)
              let object = {
                ticketCode: element.Ticket_Code,
                winner: username,
                prodId: element.productId,
              };
              console.log(object);
              self.updateWinningTicket(object);
            } else {
              self.message =
                "Game Over! Maybe next time you will win :)";
                let object = {
                ticketCode: element.Ticket_Code,
                winner: username,
                prodId: element.productId,
              };
              self.gameOver=true;
              self.displayMessage=true;
              console.log(object);
              self.updateWinningTicket(object);
            }
            // this.changeLink("url(`../assets/loosingTicket1.jpg`)")
          });
        });
        // console.log(randomTicket.PromiseResult)
        // this.fetchRandomTicket.forEach(element => {
        //   console.log(element)
        // });

        this.generateTicket = false;
      }

      //   console.log(this.numberParticlesDeleted);
      this.redraw();
    },
    redraw() {
      this.ctx = this.canvas.getContext("2d");
      // console.log(1)
      this.objects.forEach((element) => {
        //   console.log(2)
        if (element.existent) {
          // console.log(3)
        this.ctx.fillStyle = "	#F5F5F5";
          this.ctx.fillRect(
            element.coordX,
            element.coordY,
            element.width,
            element.height
          );
          //   console.log(4)
        } else {
          // console.log(4)
          this.ctx.clearRect(
            element.coordX,
            element.coordY,
            element.width,
            element.height
          );
        }
      });
    },
    next() {
      this.step += 1;
    },
    previous() {
      this.step -= 1;
    },
    
    submit() {
      
      console.log("haha");
      this.displayCheckout = false;
       // window.addEventListener("load", () => {
           let self=this;
           setTimeout(function(){

            //    console.log(this.$route.query.id)
               self.canvas=document.getElementById("canvas")
               self.ctx=this.canvas.getContext("2d")
               
               self.canvas.width=self.canvas.parentElement.clientWidth;
               self.canvas.height=self.canvas.width/2;
             //   this.ctx.fillRect(1, 1, 10, 10);
               self.canvas.addEventListener("mousedown", self.startPosition);
               self.canvas.addEventListener("mouseup", self.endPosition);
               self.canvas.addEventListener("mousemove", self.draw);
             //   this.ctx = this.canvas.getContext("2d");
               for (let i = self.canvas.width/2-self.canvas.width/5; i <self.canvas.width/2+self.canvas.width/3+self.canvas.width/10; i++) {
                 for (let j = (self.canvas.height/3); j < self.canvas.height/2+self.canvas.height/4 ; j++) {
             //   for (let i = 100; i < 400; i++) {
             //     for (let j = 50; j < 250; j++) {
                   self.square = {
                     width: 4,
                     height: 4,
                     coordX: i,
                     coordY: j,
                     existent: true,
                   };
                   self.objects.push(self.square);
                   self.numberParticles++;
                   j=j+3;
                 }
                 i=i+3;
               }
            
               self.redraw();
             // });
           },10)

    },
  },
  computed: {
    computedBackground: function() {
      return this.ticketTypeUrl;
    },
    
    ...mapGetters(["ticket"]),
  },

};
</script>
<style scoped>
canvas {
  /* border: 1px solid red; */
  background-image: url("../assets/loosingTicket1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  /* width:100%; */
  /* height:100%; */
  max-width: 650px;
  max-height:500px;
  /* width: 100%; */
  /* height: 400px; */
  margin: 0 auto;
  /* margin-top: 2em; */
}
  .test{
      width:100%;
      height:100%;
  }
</style>
