          <v-icon small left>logout</v-icon>
          Log Out
        </v-btn>
      </v-toolbar-items>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" absolute temporary right>
      <v-list>
        <v-list-item to="/" class="home">
          <v-list-item-icon>
            <v-icon small>home</v-icon>
          </v-list-item-icon>
