<script>
import { mapGetters, mapActions } from "vuex";
import { Auth } from "aws-amplify";
import LoginRegister from '../views/Login'
export default {
  name: "HelloWorld",
  components:{
    LoginRegister
  },
  data() {
    return {
      drawer: false,
      popUp: false,
      items: [
        {
          title: "Home",
          link: "/",
          icon: "home",
        },
        {
          title: "Store",
          link: "store",
          icon: "store",
        },
        {
          title: "Your Account",
          link: "account",
          icon: "mdi-account",
        },
        {
          title: "Login/Register",
          link: "login",
          icon: "login",
        },
        
      ],
    };
  },
  methods:{
    ...mapActions(["setAuthStatus", "setUser"]),
    async handleLogOut(event) {
      event.preventDefault();
      try {
        Auth.signOut();
        this.setAuthStatus(false);
        this.setUser(null);
        this.$router.push("/");
      } catch (error) {
        console.log(error.message);
      }
    },
    updatePopUp(e){
      console.log(e);
      this.popUp=false;
    }
  },
  computed: mapGetters(["allProducts", "allRandom", "isAuthenticated", "user"]),
};
</script>
