<template>
    <v-container mb-16>
        
        <v-row>
            <v-col sm="10" offset-sm="1" lg="8" offset-lg2="2">
                <h2>Latest Products: <v-btn to="store" color="primary" small text>View All</v-btn></h2>
            </v-col>
        </v-row>
        <v-row>
            <v-col sm="10" offset-sm="1" lg="8" offset-lg="2">
                <v-row>
                    <v-col
                    sm="6" 
                    md="4"
                    v-for="product in allAvailableProducts.slice(0,3)"
                    :key="product.productId"
                    >
                    <!-- {{product}} -->
                        <v-card outlined class="product" @click.stop="expand = !expand" @click="expandDetails(product.Title,product.ticketPrice,product.Description,product.Category,product.img_url,product.productId,product.Seller,product.date,product.ticketNumber,product.ticketsSold)">
                            <v-card-text > {{7 - Math.floor((date - new Date(`${product.date}`)) / 86400000)}} days left of the competition!</v-card-text>
                            <v-img :src="product.img_url" height="200px"></v-img>
                        <v-card-title>{{product.Title}}</v-card-title>
                        <v-card-subtitle>£{{product.ticketPrice}}</v-card-subtitle>
                       
                        <v-card-actions>
                            <v-btn color="success" outlined>
                                <v-icon small left>mdi-arrow-expand-all</v-icon>
                                Details
                            </v-btn>
                            
                        </v-card-actions>
                        </v-card>
                        
                    </v-col>
                </v-row>
                

            <v-row justify="center">
                <v-dialog v-model="expand" width="unset" :fullscreen="$vuetify.breakpoint.xsOnly">
                <!-- <v-col sm="10" offset-sm="1"  md="8" offset-md="2" > -->
                    
                        <v-card >
                            
                                   <v-card-title> {{7 - Math.floor((date - new Date(this.expandedProduct.date)) / 86400000)}} days left of the competition!</v-card-title>
                            <v-img :src="expandedProduct.image" min-height="100px" max-height="400px">
                                <v-btn absolute right @click.stop="expand = !expand"><v-icon >mdi-close-circle</v-icon></v-btn>
                            </v-img>
                                <v-tabs
                             v-model="tab"
                             show-arrows
                             background-color="deep-purple accent-4"
                             icons-and-text
                             dark
                             grow
                                >
                            <v-tabs-slider color="purple darken-4"></v-tabs-slider>
                            <v-tab v-for="i in tabs" :key="i.name">
                            <v-icon large>{{ i.icon }}</v-icon>
                               <div class="caption py-1">{{ i.name }}</div>
                              </v-tab>
                               <v-tab-item>
                                   <v-card>

                            <v-card-title>{{this.expandedProduct.seller+"'s "+this.expandedProduct.title}}</v-card-title>
                            <!-- <v-row> -->
                            <!-- </v-row> -->
                            <v-card-subtitle>{{this.expandedProduct.category}}</v-card-subtitle>
                            <v-spacer></v-spacer>
                        <v-card-subtitle>£{{this.expandedProduct.price}} to play.</v-card-subtitle>
                        <v-card-text>{{this.expandedProduct.number}} tickets available!</v-card-text>
                        <v-card-actions>
                            <v-btn v-if="isAuthenticated" block color="error" @click="toCheckout(expandedProduct.id,expandedProduct.ticketNumber,expandedProduct.category,expandedProduct.ticketsSold,expandedProduct.seller)" large outlined>
                                <v-icon small left>mdi-play</v-icon>
                                Play Now!
                            </v-btn>
                            <v-btn v-if="!isAuthenticated" block color="error" large outlined>
                                <v-icon small left>mdi-play</v-icon>
                                Log In to Play!
                            </v-btn>
                            
                        </v-card-actions>
                                   </v-card>
                         </v-tab-item>
                          <v-tab-item>
                            <v-card-title>{{this.expandedProduct.title}}</v-card-title>
                            <!-- <v-row> -->
                            <!-- </v-row> -->
                            <v-spacer></v-spacer>
                        <v-card-text>{{this.expandedProduct.description}}</v-card-text>
                        <v-card-actions>
                            
                        </v-card-actions>
                         </v-tab-item>
                            </v-tabs>
                        </v-card>
                    
                <!-- </v-col> -->
                </v-dialog>
            </v-row>
            </v-col>
        </v-row>
    </v-container>

</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
    data(){
        return{
            tab: 0,
            date:"",
    tabs: [
        {name:"Play", icon:"mdi-play"},
        {name:"Details", icon:"mdi-text"},
       
    ],
            products:this.$store.getters.allAvailableProducts,
            expandedProduct:{
                title:"s",
                price:"s",
                description:"s",
                category:"s",
                image:"s",
                id:"s",
                date:"",
                number:"",

            },
            // username:this.$store.getters.user.username,
            expand:false
        }
    },
    methods:{
        ...mapActions(["fetchProducts", "fetchRandom","handleUpdateProduct","setProd"]),
            toCheckout(id,ticket,category,ticketsSold,seller){
                console.log(id)
                console.log(ticket)
                this.$router.replace(`/checkout?id=${id}&number=${ticket}&username=${seller}&category=${category}&ticketsSold=${ticketsSold}`)
            },
        expandDetails(Title,ticketPrice,Description,Category,img_url,productId,seller,date,number,ticketsSold){
            this.expandedProduct.title=Title;
            this.expandedProduct.price=ticketPrice;
            this.expandedProduct.description=Description;
            this.expandedProduct.category=Category;
            this.expandedProduct.image=img_url;
            this.expandedProduct.id=productId;
            this.expandedProduct.seller=seller;
            this.expandedProduct.date=date;
            this.expandedProduct.number=number-ticketsSold;
            this.expandedProduct.ticketsSold=ticketsSold;
            this.expandedProduct.ticketNumber=number;

            console.log(this.expandedProduct)
            this.expand=true;
            console.log(this.expand)
        }
    },
    computed:mapGetters(["allAvailableProducts", "allRandom", "isAuthenticated", "user"]),
    created(){
        const myPromise= new Promise((resolve,reject)=>{
            try {
                
                this.fetchProducts();
                resolve("ok")
            } catch (error) {
                reject(error)
            }
        })
        myPromise.then(this.products=this.$store.getters.allAvailableProducts)
        this.date = new Date();
        
    }
}
</script>
<style scoped>
    .product{
        cursor: pointer;
    }
</style>