<template lang="">
  <div>
    <v-tabs
      v-model="tab"
      show-arrows
      background-color="deep-purple accent-4"
      icons-and-text
      dark
      grow
    >
      <v-tabs-slider color="purple darken-4"></v-tabs-slider>
      <v-tab v-for="i in tabs" :key="i.name">
        <v-icon large>{{ i.icon }}</v-icon>
        <div class="caption py-1">{{ i.name }}</div>
      </v-tab>
      <v-tab-item>
        <v-card class="px-4">
          <v-card-text>
            <v-form ref="loginForm" v-model="valid" lazy-validation>
              <v-row>
                <v-col cols="12">
                  <v-text-field id="username"
                    v-model="state.username"
                    label="E-mail/Username"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field id="password"
                    v-model="state.password"
                    :append-icon="show1 ? 'eye' : 'eye-off'"
                    :rules="[rules.required, rules.min]"
                    :type="show1 ? 'text' : 'password'"
                    name="input-10-1"
                    label="Password"
                    hint="At least 8 characters"
                    counter
                    @click:append="show1 = !show1"
                  ></v-text-field>
                </v-col>
                <v-col class="d-flex" cols="12" sm="6" xsm="12"> </v-col>
                <v-spacer></v-spacer>
                <v-col class="d-flex" cols="12" sm="3" xsm="12" align-end>
                  <v-btn
                    id="loginbtn"
                    x-large
                    block
                    :disabled="!valid"
                    color="success"
                    @click="onSubmitLogin"
                    ><v-icon left>login</v-icon>Login</v-btn
                  >
                </v-col>
              </v-row>
              <v-row>
                <v-col class="d-flex" cols="12">
                  <p class="success">{{ state.successLogin }}</p>
                <p class="error">{{ state.errorLogin }}</p>
                </v-col>
                </v-row>
            </v-form>
          </v-card-text>
        </v-card>
      </v-tab-item>
      <v-tab-item>
        <v-card class="px-4">
          <v-card-text>
            <v-form ref="registerForm" v-model="valid" lazy-validation>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="state.username"
                    :rules="[rules.required]"
                    label="Username"
                    maxlength="20"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="state.email"
                    :rules="[rules.required]"
                    label="Email"
                    maxlength="50"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.password"
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                    :rules="[rules.required, rules.min]"
                    :type="show1 ? 'text' : 'password'"
                    name="input-10-1"
                    label="Password"
                    hint="At least 8 characters"
                    counter
                    @click:append="show1 = !show1"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.addressLine1"
                    :rules="[rules.required]"
                    label="Address Line 1"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.addressLine2"
                    :rules="[rules.required]"
                    label="Address Line 2"
                  ></v-text-field>
                </v-col>
                <v-spacer></v-spacer>
                <v-col class="d-flex ml-auto" cols="12" sm="4" xsm="12">
                  <v-btn
                    x-large
                    block
                    :disabled="!valid"
                    color="success"
                    @click="onSubmitRegister"
                    ><v-icon left>mdi-account-plus</v-icon>Register</v-btn
                  >
                </v-col>
              </v-row>
                <v-row>
                <v-col class="d-flex" cols="12">
                  <p class="success">{{ state.successRegister }}</p>
                <p class="error">{{ state.errorRegister }}</p>
                </v-col>
                </v-row>
            </v-form>
          </v-card-text>
        </v-card>
      </v-tab-item>
      <v-tab-item>
        <v-card class="px-4">
          <v-card-text>
            <v-form
              ref="forgotForm"
              v-model="valid"
              lazy-validation
              v-if="reset == false"
            >
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.email"
                    :rules="loginEmailRules"
                    label="E-mail"
                    required
                  ></v-text-field>
                </v-col>
                <v-col class="d-flex" cols="12" sm="6" xsm="12"> </v-col>
                <v-spacer></v-spacer>
                <v-col class="d-flex" cols="12" sm="3" xsm="12" align-end>
                  <v-btn
                    x-large
                    block
                    :disabled="!valid"
                    color="success"
                    @click="handleReset"
                    @click.stop="reset = !reset"
                    ><v-icon left>mdi-lock-reset</v-icon>Reset</v-btn
                  >
                </v-col>
              </v-row>
            </v-form>
            <v-form
              v-if="reset == true"
              ref="forgotForm"
              v-model="valid"
              lazy-validation
            >
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.verificationCode"
                    :rules="[rules.required]"
                    label="Verification Code"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.email"
                    :rules="loginEmailRules"
                    label="E-mail"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="state.password"
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                    :rules="[rules.required, rules.min]"
                    :type="show1 ? 'text' : 'password'"
                    name="input-10-1"
                    label="Password"
                    hint="At least 8 characters"
                    counter
                    @click:append="show1 = !show1"
                  ></v-text-field>
                </v-col>
                <v-col class="d-flex" cols="12" sm="6" xsm="12"> </v-col>
                <v-spacer></v-spacer>
                <v-col class="d-flex" cols="12" sm="3" xsm="12" align-end>
                  <v-btn
                    x-large
                    block
                    :disabled="!valid"
                    color="success"
                    @click="resetPasswordSubmit"
                    ><v-icon left>mdi-lock-reset</v-icon>Reset</v-btn
                  >
                </v-col>
              </v-row>
              <v-row>
                <v-col class="d-flex" cols="12">
                  <p class="success">{{ state.resetMessage }}</p>
                  <p class="error">{{state.errorReset}}</p>
                </v-col>
                </v-row>
            </v-form>
          </v-card-text>
        </v-card>
      </v-tab-item>
    </v-tabs>
  </div>
</template>
<script>
import { Auth } from "aws-amplify";
import { mapActions } from "vuex";
export default {
    methods:{
        ...mapActions(["setAuthStatus", "setUser"]),
        handleReset(){
            this.reset=false;
        },
        async onSubmitLogin(e) {
            e.preventDefault();
      const { username, password } = this.state;
      console.log(username);
      console.log(password);
      try {
        const user = await Auth.signIn(
          this.state.username,
          this.state.password
        );
        console.log(user);
        this.$router.push("/");
        this.setAuthStatus(true);
        this.setUser(user);
        this.$emit('update','ok');
      } catch (error) {
        console.log(error);
        this.state.errorLogin = error.message;
      }
    },
    async onSubmitRegister() {
      const { username, password, email,postCode,addressLine1,addressLine2 } = this.state;
      console.log(username);
      console.log(email);
      console.log(password);
      console.log(postCode)
      console.log(addressLine1)
      console.log(addressLine2)
      try {
        const signUpResponse = await Auth.signUp({
          username,
          password,
          attributes: {
            email: email,
            'custom:postCode':`${postCode}`,
            'custom:addressLine1':`${addressLine1}`,
            'custom:addressLine2':`${addressLine2}`
          },
        });
        console.log(signUpResponse);
        this.state.successRegister = "Account has been created, a verification email has been sent, please verify your Account before logging in."
      } catch (error) {
        console.log(error);
        this.state.errorRegister = error.message;
      }
    },
    async resetPasswordSubmit(event){
     event.preventDefault();

      try {
      const forgotResponce=await Auth.forgotPasswordSubmit(
          this.state.email,
          this.state.verificationCode,
          this.state.password
      );
      console.log(forgotResponce)
        //  this.$router.push("/changepasswordconfirmation");
        this.state.resetMessage="You have succesfully changed your password!"
      } catch (error) {
        console.log(error)
        this.state.errorReset=error.message;
        
      }
    },
    async handleReset(e){
        e.preventDefault();
        try {
            const resetHandlerResponce= await Auth.forgotPassword(this.state.email);
        //  this.$router.push("/forgotpasswordverification");
        console.log(resetHandlerResponce)
        this.state.resetMessage="We sent you a verification code on your Email."
      }catch (error) {
        console.log(error)
        this.state.errorReset=error.message;
        this.reset=false;
      }
    }
    },
     data: () => ({
    dialog: true,
    tab: 0,
    tabs: [
        {name:"Login", icon:"mdi-account"},
        {name:"Register", icon:"mdi-account-outline"},
        {name:"Forgot Password", icon:"mdi-lock-question"}
    ],
    state: {
        username: "",
        password: "",
        errorLogin: "",
        email: "",
        postCode:"",
        addressLine1:"",
        addressLine2:"",
        verificationCode:"",
        resetMessage:"",
        // successLogin:"",
        successRegister:"",
        errorRegister:"",
        errorReset:""
      },
    valid: true,
    reset:false,
    verify: "",
    loginEmailRules: [
      v => !!v || "Required",
      v => /.+@.+\..+/.test(v) || "E-mail must be valid"
    ],
    emailRules: [
      v => !!v || "Required",
      v => /.+@.+\..+/.test(v) || "E-mail must be valid"
    ],

    show1: false,
    rules: {
      required: value => !!value || "Required.",
      min: v => (v && v.length >= 8) || "Min 8 characters"
    }
  })
}
</script>
<style lang=""></style>
