<template>
  <v-container mb-16>
    <v-row>
      <!-- <v-col md="3" offset-lg="1"></v-col> -->
      <v-col md="10" offset-lg="1">
        <v-text-field
          v-model="searchInput"
          placeholder="Search.."
          @keypress="filterProducts"
          hide-details
          prepend-icon="mdi-magnify"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row>
      <v-col md="3" offset-lg="1">
        <!-- <Sidebar @filterChange="changeFilter" v-bind:filter="this.filter"/> -->
        <div>
          <v-sheet
            v-if="$vuetify.breakpoint.mdAndUp"
            :elevation="4"
            class="pa-8"
            tile
          >
            <h3>Price</h3>
            <v-radio-group
              v-model="priceFilter"
              :mandatory="true"
              @change="filterProducts"
            >
              <v-radio
                v-for="(price, index) in prices"
                :key="index"
                :label="price"
                :value="index"
              ></v-radio>
            </v-radio-group>
            <h3>Category</h3>
            <v-radio-group
              v-model="categoryFilter"
              :mandatory="true"
              @change="filterProducts"
            >
              <v-radio
                v-for="(category, index) in categories"
                :key="index"
                :label="category"
                :value="category"
              ></v-radio>
            </v-radio-group>
          </v-sheet>
          <v-expansion-panels v-else>
            <v-expansion-panel>
              <v-expansion-panel-header expand-icon="mdi-arrow-down">
                Filters
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <h4>Prices</h4>
                <v-radio-group
                  v-model="priceFilter"
                  :mandatory="true"
                  @change="filterProducts"
                >
                  <v-radio
                    v-for="(price, index) in prices"
                    :key="index"
                    :label="price"
                    :value="index"
                    color="secondary"
                  ></v-radio>
                </v-radio-group>
                <h4>Category</h4>
                <v-radio-group
                  v-model="categoryFilter"
                  :mandatory="true"
                  @change="filterProducts"
                >
                  <v-radio
                    v-for="(category, index) in categories"
                    :key="index"
                    :label="category"
                    :value="category"
                  ></v-radio>
                </v-radio-group>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </div>
      </v-col>

      <v-col md="9" lg="7">
        <!-- <ProductDisplay v-bind:filter="this.filter"/> -->
        <div>
          <h3>Products</h3>
          <v-row v-if="displayFiltered">
            <v-col
              sm="6"
              md="4"
              v-for="product in filteredProducts"
              :key="product.productId"
            >
              <div>
                <v-card outlined class="product" @click.stop="expand = !expand" @click="expandDetails(product.Title,product.ticketPrice,product.Description,product.Category,product.img_url,product.productId,product.Seller,product.date,product.ticketNumber,product.ticketsSold)">
                  <v-img :src="product.img_url" height="200px"></v-img>
                  <v-card-title>{{ product.Title }}</v-card-title>
                  <v-card-subtitle>£{{ product.ticketPrice }}</v-card-subtitle>
                  <v-card-actions>
                    <v-btn color="success" outlined>
                      <v-icon small left>mdi-arrow-expand-all</v-icon>
                      Details
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </div>
            </v-col>
          </v-row>
          <v-row v-if="!displayFiltered">
            <v-col
              sm="6"
              md="4"
              v-for="product in allAvailableProducts"
              :key="product.productId"
            >
              <div>
                <v-card outlined class="product" @click.stop="expand = !expand" @click="expandDetails(product.Title,product.ticketPrice,product.Description,product.Category,product.img_url,product.productId,product.Seller,product.date,product.ticketNumber,product.ticketsSold)">
                  <v-img :src="product.img_url" height="200px"></v-img>
                  <v-card-title>{{ product.Title }}</v-card-title>
                  <v-card-subtitle>£{{ product.ticketPrice }}</v-card-subtitle>
                  <v-card-actions>
                    <v-btn color="success" outlined>
                      <v-icon small left>mdi-arrow-expand-all</v-icon>
                      Details
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </div>
            </v-col>
          </v-row>
              <!-- {{product}} -->
                <!-- ********************************************************************************************************************************** -->
                    <v-row justify="center">
                <v-dialog v-model="expand" width="auto " :fullscreen="$vuetify.breakpoint.xsOnly">
                <!-- <v-col sm="10" offset-sm="1"  md="8" offset-md="2" > -->
                    
                        <v-card >
                            
                                   <v-card-title> {{7 - Math.floor((date - new Date(this.expandedProduct.date)) / 86400000)}} days left of the competition!</v-card-title>
                            <v-img :src="expandedProduct.image" min-height="100px" max-height="400px">
                                <v-btn absolute right @click.stop="expand = !expand"><v-icon >mdi-close-circle</v-icon></v-btn>
                            </v-img>
                                <v-tabs
                                 v-model="tab"
                                 show-arrows
                                 background-color="deep-purple accent-4"
                                 icons-and-text
                                 dark
                                grow
    >
                            <v-tabs-slider color="purple darken-4"></v-tabs-slider>
                            <v-tab v-for="i in tabs" :key="i.name">
                            <v-icon large>{{ i.icon }}</v-icon>
                               <div class="caption py-1">{{ i.name }}</div>
                              </v-tab>
                               <v-tab-item>
                                   <v-card>

                            <v-card-title>{{this.expandedProduct.seller+"'s "+this.expandedProduct.title}}</v-card-title>
                            <!-- <v-row> -->
                            <!-- </v-row> -->
                            <v-card-subtitle>{{this.expandedProduct.category}}</v-card-subtitle>
                            <v-spacer></v-spacer>
                        <v-card-subtitle>£{{this.expandedProduct.price}} to play.</v-card-subtitle>
                        <v-card-text>{{this.expandedProduct.number}} tickets available!</v-card-text>
                        <v-card-actions>
                            <v-btn v-if="isAuthenticated" block color="error" @click="toCheckout(expandedProduct.id,expandedProduct.ticketNumber,expandedProduct.category,expandedProduct.ticketsSold,expandedProduct.seller)" large outlined>
                                <v-icon small left>mdi-play</v-icon>
                                Play Now!
                            </v-btn>
                            <v-btn v-if="!isAuthenticated" block color="error" large outlined>
                                <v-icon small left>mdi-play</v-icon>
                                Log In to Play!
                            </v-btn>
                            
                        </v-card-actions>
                                   </v-card>
                         </v-tab-item>
                          <v-tab-item>
                            <v-card-title>{{this.expandedProduct.title}}</v-card-title>
                            <!-- <v-row> -->
                            <!-- </v-row> -->
                            <v-spacer></v-spacer>
                        <v-card-text>{{this.expandedProduct.description}}</v-card-text>
                        <v-card-actions>
                            
                        </v-card-actions>
                         </v-tab-item>
                            </v-tabs>
                        </v-card>
                    
                <!-- </v-col> -->
                </v-dialog>
            </v-row>

                <!-- *********************************************************************************************************************************** -->
        </div>
      </v-col>
    </v-row>
    <!-- <Footer /> -->
  </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
// import Footer from "../components/home/Footer";
export default {
  components: {
    // Footer,
  },
  data() {
    return {
        tab: 0,
        displayFiltered:false,
        expandedProduct:{
              title:"s",
              price:"s",
              description:"s",
              category:"s",
              image:"s",
              id:"s",
              date:"",
              number:"",
              

          },
          expand:false,
      filter: {
        price: 0,
        category: 0,
       
      },
         tabs: [
           {name:"Play", icon:"mdi-play"},
          {name:"Details", icon:"mdi-text"},
        ],
      prices: [
        "any",
        "£1",
        "£2 and Under",
        "£3 and Under",
        "£4 and Under",
        "£5 and Under",
      ],
      categories: [
        "any",
        "automobiles",
        "charity",
        "clothing",
        "holiday",
        "jewelry",
        "housing",
        "sports",
        "technology",
      ],
      priceFilter: 0,
      categoryFilter: "any",
      checkbox: true,
      filteredProducts: this.$store.getters.allAvailableProducts,
      products: this.$store.getters.allAvailableProducts,
      searchInput: "",
    };
  },
  methods: {
   
    ...mapActions([
      "fetchProducts",
      "fetchRandom",
      "handleUpdateProduct",
      "setProd",
    ]),
    filterProducts() {
      this.products=this.$store.getters.allAvailableProducts;
      this.displayFiltered=true;
      if (this.searchInput == "") {
        // console.log(this.searchInput)
        if (this.priceFilter == 0 && this.categoryFilter == "any") {
          let self = this;
          this.filteredProducts = [];
          this.products.forEach((element) => {
            self.filteredProducts.push(element);
          });
        } else if (this.priceFilter == 0) {
          let self = this;
          this.filteredProducts = [];
          this.products.forEach((element) => {
            if (element.Category == self.categoryFilter) {
              self.filteredProducts.push(element);
            }
          });
        } else if (this.categoryFilter == "any") {
          let self = this;
          this.filteredProducts = [];
          this.products.forEach((element) => {
            if (element.ticketPrice <= self.priceFilter) {
              self.filteredProducts.push(element);
            }
          });
        } else {
          let self = this;
          this.filteredProducts = [];
          this.products.forEach((element) => {
            if (
              element.ticketPrice <= self.priceFilter &&
              element.Category == self.categoryFilter
            ) {
              self.filteredProducts.push(element);
            }
          });
        }
      } else if (this.searchInput != "") {
        // console.log(this.searchInput)
        if (this.priceFilter == 0 && this.categoryFilter == "any") {
          let self = this;
          this.filteredProducts = [];

          this.products.forEach((element) => {
            if (
              element.Title.toLowerCase().includes(
                `${this.searchInput.toLowerCase()}`
              )
            ) {
              self.filteredProducts.push(element);
            }
          });
        } else if (this.priceFilter == 0) {
          let self = this;
          this.filteredProducts = [];
          this.products.forEach((element) => {
            if (
              element.Category == self.categoryFilter &&
              element.Title.toLowerCase().includes(
                `${this.searchInput.toLowerCase()}`
              )
            ) {
              self.filteredProducts.push(element);
            }
          });
        } else if (this.categoryFilter == "any") {
          let self = this;
          this.filteredProducts = [];
          this.products.forEach((element) => {
            if (
              element.ticketPrice <= self.priceFilter &&
              element.Title.toLowerCase().includes(
                `${this.searchInput.toLowerCase()}`
              )
            ) {
              self.filteredProducts.push(element);
            }
          });
        } else {
          let self = this;
          this.filteredProducts = [];
          this.products.forEach((element) => {
            if (
              element.ticketPrice <= self.priceFilter &&
              element.Category == self.categoryFilter &&
              element.Title.toLowerCase().includes(
                `${this.searchInput.toLowerCase()}`
              )
            ) {
              self.filteredProducts.push(element);
            }
          });
        }
      }
    },
    toCheckout(id,ticket,category,ticketsSold,seller){
                console.log(id)
                console.log(ticket)
                this.$router.replace(`/checkout?id=${id}&number=${ticket}&username=${seller}&category=${category}&ticketsSold=${ticketsSold}`)
            },
        expandDetails(Title,ticketPrice,Description,Category,img_url,productId,seller,date,number,ticketsSold){
            this.expandedProduct.title=Title;
            this.expandedProduct.price=ticketPrice;
            this.expandedProduct.description=Description;
            this.expandedProduct.category=Category;
            this.expandedProduct.image=img_url;
            this.expandedProduct.id=productId;
            this.expandedProduct.seller=seller;
            this.expandedProduct.date=date;
            this.expandedProduct.number=number-ticketsSold;
            this.expandedProduct.ticketsSold=ticketsSold;
            this.expandedProduct.ticketNumber=number;

            console.log(this.expandedProduct)
            this.expand=true;
            console.log(this.expand)
        }
  },
  computed:mapGetters(["allAvailableProducts", "allRandom", "isAuthenticated", "user"]),
  created() {
    // mapGetters(["allAvailableProducts", "allRandom", "isAuthenticated", "user"]);
    const myPromise= new Promise((resolve,reject)=>{
            try {
                
    this.fetchProducts();
    // this.products = this.$store.getters.allAvailableProducts;
    this.date = new Date();
     } catch (error) {
                reject(error)
            }
        })
        myPromise.then(this.products=this.$store.getters.allAvailableProducts)
  },
  
 
  
};
</script>
<style scoped>
.product{
        cursor: pointer;
    }
    </style>
